import { Fragment, Suspense } from 'react';
import { Route, Routes } from 'react-router-dom';

import { MainLayout, MainLayoutType } from 'src/layouts/MainLayout';
import { NotFoundView } from 'src/errors/NotFoundView';
import { Loading } from 'src/common';
import CitiesListPage from './pages/CitiesListPage';
import CityDetailsPage from './pages/CityDetailsPage';

type RouteType = {
  path: string;
  layout?: MainLayoutType;
  // any type because of lazy(() => import())
  element: any;
};

type RoutesType = RouteType[];

const renderRoutes = (routes: RoutesType = []) => (
  <Suspense fallback={<Loading />}>
    <Routes>
      {routes.map((route: RouteType) => {
        const Layout = route.layout || Fragment;
        const Component = route.element;

        return (
          <Route
            key={route.path}
            path={route.path}
            element={
              <Layout>
                <Component />
              </Layout>
            }
          />
        );
      })}
    </Routes>
  </Suspense>
);

const routes: RoutesType = [
  {
    path: '/',
    layout: MainLayout,
    element: CitiesListPage,
  },
  {
    path: '/citiesList',
    layout: MainLayout,
    element: CitiesListPage,
  },
  {
    path: '/cityDetails/:cityName',
    layout: MainLayout,
    element: CityDetailsPage,
  },
  {
    path: '*',
    layout: MainLayout,
    element: NotFoundView,
  },
];

export const allRoutes = renderRoutes(routes);
